        this.message =
          right_time[Math.floor(Math.random() * right_time.length)];
      }
    },
    onClickVideo() {
      this.audio.pause();
      this.isPlaying = false;
    },
    playSong() {
      if (!this.isPlaying) {
        this.songName = this.randomSong;
        let song = require("@/assets/songs/" + this.songName);
        if (this.isDateNow) {
          song = require("@/assets/songs/" + "daylight.mp3");
          // song = require("@/assets/songs/" + "the1.mp3");
        }
        this.audio = new Audio(song);
        this.audio.volume = 0.52;
        // this.audio.play();
        this.isPlaying = true;
      }
    },
    getTimeNow() {
      const today = new Date();
      let hours = today.getHours() % 12;
      let ampm = today.getHours() <= 12 ? " AM" : " PM";
      const date =
        today.getFullYear() +
        "/" +
        (today.getMonth() + 1) +
        "/" +
        today.getDate();
      this.dateNow = date;
      const time =
        hours + ":" + String(today.getMinutes()).padStart(2, "0") + ampm;
      this.secondsNow = today.getSeconds();
      this.today = [date, time + " PST"];
    },
