<template>
  <canvas id="oli-bg"></canvas>
</template>

<script>
export default {
  name: "OliBackGround",
  components: {},

  mounted() {
    let canvas, ctx, w, h, circles;
    let colors = [
      // [0, 255, 255],
      // [255, 255, 255],
      // [156, 236, 255],
      // [156, 236, 255],
      // [186, 159, 231],
      // [158, 116, 208],
    ];
    // let colors = [[128, 128, 128]];
    function init() {
      canvas = document.querySelector("#oli-bg");

      ctx = canvas.getContext("2d");
      resizeReset();
      animationLoop();
    }
    function resizeReset() {
      w = canvas.width = window.innerWidth;
      h = canvas.height = window.innerHeight;
      circles = [];
      createObject();
    }

    function createObject() {
      for (let i = 0; i < 52; i++) {
        circles.push(new Circle(i));
      }
    }

    function animationLoop() {
      ctx.clearRect(0, 0, w, h);
      ctx.globalCompositeOperation = "luminosity";
      drawScene();
      requestAnimationFrame(animationLoop);
    }

    function drawScene() {
      circles.map((circle) => {
        circle.update();
        circle.draw();
      });
    }

    function easeInOutQuad(x) {
      return x < 0.5 ? 2 * x * x : 1 - Math.pow(-2 * x + 2, 2) / 2;
    }

    class Circle {
      constructor(i) {
        this.x = Math.random() * w;
        this.y = Math.random() * h;
        this.radius = Math.random() * 0.5 + 0.75;
        this.color = colors[i % colors.length];
        this.alpha = Math.random() * 1 + 0.5;
        if (i % colors.length === 4) {
          this.x = Math.random() * w - 100;
          this.y = Math.random() * h - 100;
          this.alpha = Math.random() * 1 + 0.45;
        }
        this.tick = 0;
        this.ttl = 0;
      }
      draw() {
        ctx.beginPath();
        ctx.arc(this.x, this.y, this.radius, 0, Math.PI * 2);
        ctx.fillStyle = `rgba(${this.color[0]},${this.color[1]},${this.color[2]},${this.alpha})`;
        ctx.fill();
        ctx.closePath();
      }

      update() {
        if (this.tick >= this.ttl) {
          this.setTarget();
        }
        let progress = this.tick / this.ttl;
        this.x =
          this.startX + (this.targetX - this.x) * easeInOutQuad(progress);
        this.y =
          this.startY + (this.targetY - this.y) * easeInOutQuad(progress);
        this.tick += 2;
      }

      setTarget() {
        this.startX = this.x;
        this.startY = this.y;
        this.targetX = this.x + (Math.random() * 200 - 100);
        this.targetY = this.y + (Math.random() * 200 - 100);
        this.tick = 0;
        this.ttl = Math.random() * 180 + 180;
      }
    }

    init();
  },
};
</script>

<style scoped>
#oli-bg {
  position: fixed;
  left: 0px;
  top: 0px;
  height: 100vh;
  width: 100vw;
  margin: 0px;
  padding: 0px;
  z-index: -10;

  background: radial-gradient(circle, rgb(44, 0, 65) 0%, rgb(6, 0, 54) 75%);

  background-image: url("../assets/daylight.gif");
  /* background-image: url("../assets/deep_space.gif"); */
  background-repeat: no-repeat;
  background-size: cover;
}
</style>
